/* graph style */
.graphBox{
  padding: 10px 30px;
}
.graphCanvas{
  height:300px;
}
.graphTitle{
  font-size: 18px;
  color:#465574;
}
.monthLabel{
  border-top: 1px solid #e7e7e7;
  margin:0px 10px;
  padding: 0px;
}
.monthLabel li{
  display: inline-block;
  font-size: 11px;
  width:8.3%;
  color:#c0c1ca;
  letter-spacing: 0.02rem;
}